import { Collections } from './../../app/app.models';
import { DataProvider } from './../../providers/data/data';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Content } from '../../app/app.models';

@IonicPage({
  segment: 'materia-alt/:id'
})
@Component({
  selector: 'page-materia-alt',
  templateUrl: 'materia-alt.html',
})
export class MateriaAltPage {

  public id: string = this.navParams.get('id');

  public materia: Content;
  public children: Content[];

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private data: DataProvider,
  ) {
  }

  ionViewDidLoad() {

    this.loadContent(this.id);
    this.loadChildren(this.id);

  }

  async loadContent(id: string) {

    this.materia = await this.data.getDocAlt<Content>(Collections.CONTENT, id);

  }

  async loadChildren(id: string) {
    const children = await this.data.getCollectionQuery<Content>(Collections.CONTENT, ref => ref.where('parent_id', '==', id));
    this.children = children.filter(c => c.liberadoInPrograma);
  }

}



